<template>
    <transition name="modal">
        <div class="modal-mask">
          <div class="modal-wrapper">
            <div id="modal_container" class="modal-container">

              <div class="modal-header">
                <slot name="header">
                 
                   default header
                 
                </slot>
              </div>

              <div class="modal-body">
                <slot name="body">
                  default body
                </slot>
              </div>

              <div class="modal-footer">
                <slot name="footer">
                  default footer
                  <button class="modal-default-button" @click="$emit('close')">
                    OK
                  </button>
                </slot>
              </div>
              
            </div>
          </div>
        </div>
      </transition>
</template>

<script>
export default {
    name: 'ModalWindow',
    created() {
        window.addEventListener("keyup", this.onEscapeKeyUp);
    },
    methods: {
        onEscapeKeyUp(event) {
            if (event.which === 27) {
                this.$emit("close");
            }
        }
    }
}
</script>

<style scoped>
    .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    transition: opacity 0.3s ease;
    }

    .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
    }

    .modal-container {
    max-width: 375px;
    width: 100%;
    margin: 0px auto;
    padding: 15px 20px;
    background-color: #fff;
    border-radius: 0;
    box-shadow: 0px 0px 15px rgba(152 197 235 / 60%);
    transition: all 0.3s ease;
    font-family: 'Montserrat', sans-serif;
    }

    .modal-header h3 {
    margin-top: 0;
    color: #42b983;
    }

    .modal-body {
    margin: 20px 0;
    }

    .modal-default-button {
        float: right;
    }

    .modal-enter {
        opacity: 0;
    }

    .modal-leave-active {
        opacity: 0;
    }

    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }
</style>